@import url(https://fonts.googleapis.com/css?family=DM+Sans:400&subset=latin,latin-ext);
@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:400&subset=latin,latin-ext);
@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:400&subset=latin,latin-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'Open Sans', 'Lato',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@-moz-document url-prefix() {
  .background {
    background: radial-gradient(farthest-corner at 40px 40px,
      #6076C4 0%, #C460A5 75%, #28235F 100% );
  }
}

.header {
    position: absolute;

    display: flex;
    flex-direction: row;
    align-items: space-between;
    justify-content: top;

    text-decoration: none;

    margin-top: 50px;
    height: 50px;
    width: 100vw;
}

.header .right {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;

    margin-right: 50px;
}

.nav-button {
    display: flex;
    flex-direction: row;
    align-items: right;
    justify-content: right;

    height: 3.125em;
    width: 9.375em;

    font-family: DM Sans;
    font-size: 1em;
    font-style: normal;
    letter-spacing: 0.1em;
    text-align: right;
    color: #000000;

    background: transparent;

    margin-right: 25px;
}

.nav-button .text {
    line-height: 0.5;
    border-bottom: 2px solid transparent;
}

.nav-button .text:hover {
    line-height: 0.5;
    border-bottom: 2px solid black;
}


.rectangle-polaroid {
    min-width: 20em;
    height: 100%;

    background: white;
    border-radius: 15px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;

    transition: transform .3s;

    margin-left: 1em;
    margin-right: 1em;
    margin-bottom: 1em;
}

.rectangle-polaroid .image {
    width: 100%;
    border-radius: 15px 15px 0px 0px;
    overflow: hidden;
}

.rectangle-polaroid .text {
    background: white;
    border-radius: 15px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow: hidden;
}

.rectangle-polaroid .text .title {
    font-family: DM Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 1em;
    line-height: 1.75em;
    color: #000000;

    margin-top: 2em;
    margin-bottom: 0em;
    margin-left: 2em;
    margin-right: 2em;
}

.rectangle-polaroid .text .subtitle {
    font-family: DM Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 1em;
    line-height: 1.75em;
    color: #0047AB;

    margin-top: 0em;
    margin-bottom: 0em;
    margin-left: 2em;
    margin-right: 2em;
}

.rectangle-polaroid .text .description {
    font-family: DM Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 1em;
    line-height: 1.75em;
    color: #000000;

    margin-top: 1em;
    margin-bottom: 2em;
    margin-left: 2em;
    margin-right: 2em;
}

.rectangle-polaroid:hover {
    box-shadow: 6px 6px 6px rgba(0, 0, 0, 0.025);
}



.icon-button {
    height: 40px;
    width: 40px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    font-size: 16px;
    background: transparent;
    border-radius: 15px;
    color: black;
}

.icon-button:hover {
    background: black;
    color: white;
}

.icon-button .icon {
    text-decoration: none;
}

.landing {
    background: #F6F8FB;
    min-height: 100vh;

    font-family: DM Sans;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.landing .introduction {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    height: 100%;

    margin-top: 20vh;
    margin-bottom: 1em;
}

.landing .introduction .header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    font-size: 1.5em;
    font-style: normal;
    font-weight: 700;
    line-height: 2em;
}

.landing .row {
    height: 100%;
    width: 60vw;

    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;

    overflow-y: hidden;
    overflow-x: auto;

    margin-top: 3em;
    margin-bottom: 1em;
}

.landing .row .header {
    font-size: 2.5em;
    font-style: normal;
    font-weight: 700;
    text-align: left;
}

.landing .icon-row {
    display: flex;
    flex-direction: row;
}

.image {
    height: 12em;
    width: 15em;
    object-fit: cover;
    border-radius: 15px 0px 0px 15px;
}

::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 7px;
}

::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: rgba(0, 0, 0, .15);
  box-shadow: 0 0 1px rgba(255, 255, 255, .5);
}

@media (max-width: 900px) {
    
    .landing .row {
        width: 80%;
        flex-direction: column;
        margin-bottom: 1em;
    }

    .landing .icon-row {
        margin-bottom: 2em;
    }
}

.about {
    min-height: 100vh;
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;

    background: #F6F8FB;
    background-size: cover;
}

.about .centered {
    width: 60%;
    height: 100%;

    margin-top: 20vh;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    background-size: cover;
}

.about .centered .big-text {
    font-family: DM Sans;
    font-size: 1.5em;
    font-style: normal;
    font-weight: 700;
    text-align: left;

    margin-top: 0px;
    margin-bottom: 0px;
}

.about .centered .icon-row {
    height: 50px;
    width: 250px;

    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    margin-top: 10px;
    margin-bottom: 50px;
}

.about .content {
    width: 100%;

    margin-bottom: 25px;
    margin-top: 15px;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.about .content .text {
    font-family: DM Sans;
    font-size: 1em;
    font-style: normal;
    font-weight: 400;
    margin-bottom: 0px;
    line-height: 1.5em;
}

.about .content .left-panel {
    width: 50%;
    text-align: left;
}

.about .content .right-panel {
    width: 45%;
    text-align: right;
}

.about .spotify-embed {
    margin-top: 15px;
    border-radius: 15px;
}

@media (max-width: 1000px) {
    .about .content {
        width: 100%;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
    }

    .about .content .left-panel {
        width: 100%;
        text-align: center;
    }

    .about .content .right-panel {
        margin-top: 10px;
        width: 100%;
        text-align: center;
    }
}
.spotify {
    min-height: 100vh;
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;

    background: #F6F8FB;
    background-size: cover;

    max-width: 100%;
    overflow-x: hidden;
}

.spotify::-webkit-scrollbar {
   display: none;
}

.spotify .top {
    width: 60%;
    height: 100%;

    margin-top: 20vh;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    background-size: cover;
}

.spotify .top .imageContainer {
    height: 15em;
    width: 100%;
    overflow: hidden;
}

.spotify .top .image {
    width: 100%;
    border-radius: 15px 15px 0px 0px;
    object-fit: cover;
}

.spotify .bottom .imageContainer {
    margin-top: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.spotify .bottom .image {
    height: 100%;
    width: 50%;
    border-radius: 15px;
    object-fit: cover;
}

.spotify .top .big-text {
    font-family: DM Sans;
    font-size: 1.25em;
    font-style: normal;
    font-weight: 700;
    text-align: left;

    line-height: 2em;

    margin-top: 0px;
    margin-bottom: 0px;
}

.spotify .top .text {
    font-family: DM Sans;
    font-size: 1em;
    font-style: normal;
    font-weight: 500;
    text-align: center;

    line-height: 1.5em;

    margin-top: 0px;
    margin-bottom: 0px;
}

.spotify .bottom {
    width: 60%;
    margin-bottom: 50px;
}

.spotify .bottom .text {
    font-family: DM Sans;
    font-size: 1em;
    font-style: normal;
    font-weight: 400;
    margin-bottom: 0px;
    line-height: 1.5em;
    text-align: left;
}

.spotify .spacer {
    height: 15px;
}

@media (max-width: 800px) {
    .spotify .top {
        width: 60%;
        height: 100%;

        margin-top: 20vh;

        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;

        background-size: cover;
    }

    .spotify .top .left-container .big-text {
        font-size: 1em;
    }

    .spotify .top .right-container .text {
        font-size: 0.8em;
    }

    .spotify .bottom .text {
        font-size: 0.8em;
    }
}

